import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

class TabBar extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {
		const props = this.props;
		const {
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('tab-bar',className);

		return (
			<ul
				{...others}
				className={klassName}
				role="tabbar"
			>
				{children}
			</ul>
		);
	}
};

TabBar.propTypes = {
	className: PropTypes.string,
	children: PropTypes.oneOfType([
		PropTypes.object,
		PropTypes.array
	])
};

export default TabBar;